.options{
  width: 500px;
  border: 1px solid black;
  /* background-color: rgb(218, 218, 218); */
  background-color: white;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.6);
          box-shadow: 0 4px 8px 0 rgba(0,0,0,0.6);
  border-radius: 15px;
  padding: 10px;
  border: 1px solid gainsboro;
}

.price {
  padding: 0.5rem;
  font-size: 24px;
}

.color-picker {
  padding: 0.5rem;
}
.color-picker span > div {
  border: 1px solid black;
  border-radius: 0!important;
}

.circle-picker {
  margin-top: 10px;
}
.circle-picker span div span > div {
  border: 1px solid black;
  /* border-radius: 0!important; */
}

.dropzone {
  height: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  border: 1px dashed;
}

.text-container {

  padding: 0.5rem
}

.logo-container{
  padding: 1rem 0;
}

#order_btn {
  background-color: rgb(106, 198, 91);
  color: white;
  font-size: 18px;
  font-weight: 700;
}

@media (max-width: 500px){
  .options{
    width: 100vw;
  }
}


